<template>
  <div>
    <van-cell-group>
      <list
        v-model="loading"
        :finished="finished"
        :error.sync="error"
        @load="getTopTeacherBalanceDetail"
      >
        <template v-for="(item, index) in refererClasses">
          <van-cell
            :key="index"
            :title="item.StudentName"
          />
        </template>
      </list>
    </van-cell-group>
  </div>
</template>

<script>
import createList from "@/utils/create/create-list";
export default createList({
  data() {
    return {
      refererClasses: []
    };
  },
  created() {
    this.getTopTeacherBalanceDetail();
  },
  methods: {
    getTopTeacherBalanceDetail() {
      this.loading = true
      this.$store
        .dispatch("getTopTeacherBalanceDetail", {
          ...this.page()
        })
        .then(({ data, total }) => {
          this.refererClasses = this.refererClasses.concat(data);
          this.loading = false;
          this.total = this.refererClasses.length;
          this.complateLoad(total);
        })
        .catch(() => {
          this.errorLoad();
        });
    }
  }
});
</script>
