<template>
  <div class="clz-tab">
    <tabs v-model="active">
      <template v-for="(item, index) in tabs">
        <tab
          :title="item.name"
          :key="index"
        >
          <component
            :is="currentTabComponent(item.component)"
          />
        </tab>
      </template>
    </tabs>
  </div>
</template>

<script>
import { Tab, Tabs } from "vant";
import TabRecruit from "./components/tab-recruit";
import TabReferer from "./components/tab-referer";
export default {
  components: { Tab, Tabs, TabRecruit, TabReferer },
  data() {
    return {
      active: 0,
      tabs: [
        {
          component: "Recruit",
          name: "招生收益"
        },
        {
          component: "Referer",
          name: "推广收益"
        }
      ]
    };
  },
  methods: {
    currentTabComponent(component) {
      return "tab-" + component.toLowerCase();
    }
  }
};
</script>

<style lang="less" scoped>
.clz-tab {
  position: sticky;
  top: 50px;
}
</style>
