import createBasic from './createBasic'
import createList from '@/mixins/create-list'
import {
  List
} from 'vant'
export default function (sfc) {
  sfc.mixins = sfc.mixins ? sfc.mixins.concat(createList) : [createList]
  sfc.components = Object.assign(sfc.components || {}, {
    List
  })
  return createBasic(sfc)
}
