export default {
  data () {
    return {
      loading: false,
      finished: false,
      error: false,
      PageIndex: 0,
      PageSize: 10,
      total: 0
    }
  },
  methods: {
    page () {
      return {
        PageIndex: this.PageIndex,
        PageSize: this.PageSize
      }
    },
    complateLoad (total) {
      if (this.total === total) {
        this.finished = true
      } else {
        this.finished = false
        this.PageIndex = this.PageIndex + 1
      }
    },
    errorLoad () {
      this.error = true
      this.finished = true
      this.loading = false
    }
  }
}
