export default {
  data() {
    return {
      balanceData: {}
    }
  },
  methods: {
    getBalance() {
      this.$store
        .dispatch('getBalance')
        .then((data) => {
          this.balanceData = data
          this.$store.dispatch('balanceChange', data.Balance)
        }).catch(() => {

        })
    }
  }
}
